import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { TabsPage } from './tabs.page';

const routes: Routes = [
  {
    path: '',
    component: TabsPage,
    children: [
      {
        path: 'overviews',
        loadChildren: () => import('../overviews/overviews.module').then(m => m.OverviewsPageModule)
      },
      {
        path: 'reports',
        loadChildren: () => import('../reports/reports.module').then(m => m.ReportsPageModule)
      },
      {
        path: 'products',
        loadChildren: () => import('../products/products.module').then(m => m.ProductsPageModule)
      },
      {
        path: 'sell',
        loadChildren: () => import('../sell/sell.module').then(m => m.SellPageModule)
      },
      // {
      //   path: 'other',
      //   loadChildren: () => import('../other/other.module').then(m => m.OtherPageModule)
      // },
      {
        path: '',
        redirectTo: 'overviews',
        pathMatch: 'full'
      }
    ]
  },
  {
    path: '',
    redirectTo: 'overviews',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TabsPageRoutingModule { }
